
body {
    background-color: white;
    font-family: 'Mulish', sans-serif;
    font-size: large;
    
}

.current {
  color: green;
}


.layout-wrapper {

  width: 97.5%;
  height: 100%;
  max-width: 850px;
  margin: auto;
  padding-left: 30px;
  padding-right: 30px;
}

.header-wrapper {
  
  width: 100%;
}

.header-container {

  width: 100%;
  height: 80px;
  line-height: 80px;
}

.header-links{
  float: right;
  
}

.header-links a {
    margin-right: 10px;
    margin-left: 10px;
    text-decoration: none;
    
}

.header-logo-icon{
  font-size: 24px;
}

.header-logo-text{
  margin-bottom: 300px;
  margin-left: 7.5px;
}

.homepage-wrapper{
  margin-bottom: 80px;
  margin-top: 80px;
}

.introduction-title{
  font-size: 34px;
}

.homepage-projects{
  margin-top: 80px;
}

.homepage-projects h2{
  font-size: 24px;
}

.homepage-project h3{
  font-size: 19px;
  display: flex;
  align-items: center;
  margin-right: 10px;
}

.homepage-project{
    display: flex;
    justify-content: space-between;
    padding: 20px 0;
    border-bottom: 1px solid #eee;
    align-items: center;
    margin-right: 16px;
    margin-left: 16px;
}

.homepage-project p {
  font-size: 17px;
  
  
  
}

.button-view {
    width: 100%;
    max-width: 70px;
    background-color: #665df5;
    border: none;
    color: #fff;
    border-radius: 4px;
    height: 48px;
    font-size: 14px;
    font-weight: 700;
    min-width: 60px;
    
}